/* Services.css */
.services-section {
  padding: 6rem 2rem;
  background: #2B221C;
  position: relative;
  overflow: hidden;
  opacity: 0;
  transform: translateY(50px);
  transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.services-title {
  font-size: 2.8rem;
  font-family: 'Times New Roman', serif;
  color: #E6B07C;
  text-align: center;
  margin-bottom: 4rem;
  letter-spacing: 3px;
  text-shadow: 0 4px 12px rgba(230, 176, 124, 0.3);
}

.honeycomb-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  padding: 2rem;
  perspective: 1000px;
}

.hexagon-card {
  position: relative;
  height: 300px;
  background: #453025;
  transition: all 0.6s cubic-bezier(0.4, 0, 0.2, 1);
  transform-style: preserve-3d;
  cursor: pointer;
  animation: float 4s ease-in-out infinite;
}

@keyframes float {
  0%, 100% { transform: translateY(0) rotateX(0deg) rotateY(0deg); }
  50% { transform: translateY(-20px) rotateX(5deg) rotateY(5deg); }
}

.hexagon-card:hover {
  transform: translateY(-10px) scale(1.05) rotateX(10deg) rotateY(10deg);
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.4);
}

.hex-content {
  position: relative;
  z-index: 2;
  padding: 2rem;
  color: #D6B58C;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.hex-icon {
  font-size: 2.5rem;
  margin-bottom: 1.5rem;
  filter: drop-shadow(0 4px 6px rgba(230, 176, 124, 0.4));
}

.hex-content h3 {
  font-size: 1.4rem;
  margin-bottom: 1rem;
  color: #E6B07C;
}

.hex-content p {
  font-size: 0.95rem;
  opacity: 0;
  transform: translateY(20px);
  transition: all 0.4s ease;
}

.hexagon-card:hover p {
  opacity: 1;
  transform: translateY(0);
}

.hex-gradient {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, 
    transparent 0%, 
    rgba(230, 176, 124, 0.1) 50%, 
    transparent 100%);
  opacity: 0;
  transition: opacity 0.4s ease;
}

.hexagon-card:hover .hex-gradient {
  opacity: 1;
}

@media (max-width: 768px) {
  .honeycomb-grid {
    grid-template-columns: 1fr;
  }

  .hexagon-card {
    height: 250px;
    clip-path: polygon(50% 0%, 100% 25%, 100% 75%, 50% 100%, 0% 75%, 0% 25%) !important;
  }

  .services-title {
    font-size: 2.2rem;
  }
}

@keyframes hexReveal {
  from {
    opacity: 0;
    transform: scale(0.5) rotate(45deg);
  }
  to {
    opacity: 1;
    transform: scale(1) rotate(0);
  }
}